import * as styles from "../../../styles/restaurant.module.css";
import { useDispatch, useSelector } from "react-redux";
import React from 'react'
import SocialMediaIcon from "../../icons"

const RestaurantMedia = () => {

    const dispatch = useDispatch();
    const restaurant = useSelector((state) => state.restaurant)

    return (
        <div className={styles.restaurant_home_media}>
            {
                restaurant.restaurantInfo.map((tableline, index) => {
                    if( (tableline[0] === 'map') && (tableline[1] != undefined) ) {
                        return (
                            <a key={index} className={styles.restaurant_home_item} href={tableline[1]} target="_blank">

                                <SocialMediaIcon app={'maps'} />
                            </a>
                        )
                    }
                    else if( (tableline[0] === 'facebook') && (tableline[1] != undefined) ) {
                        return (
                            <a key={index} className={styles.restaurant_home_item} href={tableline[1]} target="_blank">

                                <SocialMediaIcon app={'facebook'} />
                            </a>
                        )
                    }
                    else if( (tableline[0] === 'twitter') && (tableline[1] != undefined) ) {
                        return (
                            <a key={index} className={styles.restaurant_home_item} href={tableline[1]} target="_blank">

                                <SocialMediaIcon app={'twitter'} />
                            </a>
                        )
                    }
                    else if( (tableline[0] === 'instagram') && (tableline[1] != undefined) ) {
                        return (
                            <a key={index} className={styles.restaurant_home_item} href={tableline[1]} target="_blank">

                                <SocialMediaIcon app={'instagram'} />
                            </a>
                        )
                    }
                    else {
                        return null
                    }
                })
            }
        </div>
    )
}
export default RestaurantMedia
